import { Component } from '@angular/core';
import { FLAGS, TEXTILE_PRINTS, ADVERTISING, UNIFORMS, SEARCH } from 'src/app/models/product-and-services';
import { Product } from 'src/app/models/product';

@Component({
  selector: 'app-services',
  templateUrl: './services.component.html',
  styleUrls: ['./services.component.scss']
})
export class ServicesComponent {
  flags = FLAGS;
  textilePrints = TEXTILE_PRINTS;
  advertising = ADVERTISING;
  uniforms = UNIFORMS;
  search = SEARCH;
  all:Product[  ] = [];
  sectionSelected;
  
  constructor() {
    this.sectionSelected = this.flags;
    this.all = this.all.concat( this.flags.items );
    this.all = this.all.concat( this.textilePrints.items );
    this.all = this.all.concat( this.advertising.items );
    this.all = this.all.concat( this.uniforms.items );
    this.search.items = this.all;
  }

  searchProduct( value: string ){
    this.search.items = this.all.filter( product => product.name.toLocaleLowerCase().includes(value.toLocaleLowerCase()) );
    this.search.description = `Resultado de \"${ value }\"`;
    this.sectionSelected = this.search;
  }

  sectionChange( section: any ){
    this.sectionSelected = section;
  }
}
