export class Project{
    name: String;
    category: String;
    image: String;
    intro: String;
    description: String;
    date: String;
    client: String;

    constructor( name: String, category: String, image: String, 
            intro?: String , description?: String, date?: String, client?: String ) {
            this.name = name || '' ;
            this.intro = intro || '';
            this.image = image || '';
            this.description = description || '';
            this.date = date || '';
            this.client = client || '';
            this.category = category || '';
    }
}